$watch-size:550px;
$center-circle-size: 40px;
$seconds-hand-height: ($watch-size / 2) - ($center-circle-size / 2);
$watch-minute-hand-top: 40px;
$minute-hand-height:
($watch-size / 2) - ($center-circle-size / 2) - $watch-minute-hand-top;
$digit-position-vertical: $watch-size - (($watch-size/5) + 20px);
$digit-position-horizontal: $watch-size - (($watch-size/8) + 20px);
$digit-font-size: $watch-size/7.5;
